* {
  box-sizing: border-box;
  margin:0;
  padding:0;
}

.App {
  text-align: center;
  background-color: #282c34;
  min-height: 100vh;
  color: white;
  font-size: calc(10px + 2vmin);
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.twitter {
  padding-top: 80px;
  align-self: center;
}

nav {
  height: 60px;
  width: 100%;
  z-index: 2;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  background-color: #282c34;
  box-shadow: 0 1px 5px #00000066;
}

.sidebar {
  height: 100vh;
  background: #ffb142;
  width:170px;
  transition: 2ms ease-in-out;
  color: white;
  z-index: 4;
}

.sidebar li {
  list-style: none;
  padding: 40px 0;
}

nav h2 {
  color: #ffb142;
  margin: 0 auto;
  align-self: center;
}

nav .hamburger {
  width: 80px;
  height: 80px;
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  top: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  cursor: pointer;
}

nav .stick {
  width: 40px;
  height: 5px;
  border-radius: 4px;
  margin-bottom: 6px;
  background-color: #ffb142;
  display: inline-block;
}

nav .stick:last-child {
  margin-bottom: 0;
}

.roulette {
  box-sizing: border-box;
  align-self: center;
  width: 100%;
  margin: 0 auto;
  padding: 50px 15px 0;

}

.roulette h1 {
  font-size: 3rem;
  margin: 60px 0;
}

.roulette input {
  font-size: 30px;
  padding: 10px;
  box-sizing: border-box;
  border-radius: 5px;
  width: 50%;
}

.roulette input:before {
  content: 'min/max';
  position: relative;
  display:block;
}

.roulette button {
  padding: 20px 50px;
  background: #ffb142;
  border-radius:10px;
  outline: none;
  border-color: transparent;
  display: block;
  margin: 20px auto;
  width: 100%;
  color: white;
  font-size:30px;
}

@media (min-width: 760px) {
  .twitter {
   width:100%;
  }
  .roulette {
    width:80%;
    padding: 200px 15px 0;
  }
  .sidebar {
    width:300px;
  }

}